@tailwind base;
@tailwind components;
@tailwind utilities;

/* oswald-regular - latin-ext_latin */
@font-face {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    src: url('./fonts/oswald-v48-latin-ext_latin-regular.eot');
    /* IE9 Compat Modes */
    src: local(''),
        url('./fonts/oswald-v48-latin-ext_latin-regular.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */
        url('./fonts/oswald-v48-latin-ext_latin-regular.woff2') format('woff2'),
        /* Super Modern Browsers */
        url('./fonts/oswald-v48-latin-ext_latin-regular.woff') format('woff'),
        /* Modern Browsers */
        url('./fonts/oswald-v48-latin-ext_latin-regular.ttf') format('truetype'),
        /* Safari, Android, iOS */
        url('./fonts/oswald-v48-latin-ext_latin-regular.svg#Oswald') format('svg');
    /* Legacy iOS */
}

/* oswald-600 - latin-ext_latin */
@font-face {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 600;
    src: url('./fonts/oswald-v48-latin-ext_latin-600.eot');
    /* IE9 Compat Modes */
    src: local(''),
        url('./fonts/oswald-v48-latin-ext_latin-600.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */
        url('./fonts/oswald-v48-latin-ext_latin-600.woff2') format('woff2'),
        /* Super Modern Browsers */
        url('./fonts/oswald-v48-latin-ext_latin-600.woff') format('woff'),
        /* Modern Browsers */
        url('./fonts/oswald-v48-latin-ext_latin-600.ttf') format('truetype'),
        /* Safari, Android, iOS */
        url('./fonts/oswald-v48-latin-ext_latin-600.svg#Oswald') format('svg');
    /* Legacy iOS */
}

body {
    font-family: 'Oswald', sans-serif;
    color: white;
}

.oswald {
    font-family: 'Oswald', sans-serif;
}